<template>
  <div class="search-result">
    <div class="scroll-wrap" ref="wrapper">
      <div class="wrapper" ref="content">
        <cube-scroll>
          <div class="page page-form-search-result">
            <v-row :gutter.Number="30">
              <v-search :autofocus="false" v-model="keywords" :hiddenStatus="hiddenStatus"> </v-search>
            </v-row>

            <div v-show="noData" class="no-result">
              <p class="font">暂无相关信息</p>
            </div>
          </div>
          <!--全选按钮-->
          <div class="allSelect" v-if="data2.length > 0 && keywords == ''">
            <span class="icon-all-select" :class="{'active': showAllSelect}" @click="allSelectClick"></span>
            <span class="allText">全选</span>
          </div>
          <!--tree组件-->
          <div class="tree" v-if="data2.length > 0">
            <el-tree
              @check="getKeys(data)"
              @node-expand="refreshHeight"
              class="filter-tree"
              node-key="id"
              show-checkbox
              :data="data2"
              :props="defaultProps"
              :filter-node-method="filterNode"
              :default-checked-keys="allSelectArr"
              ref="tree2">
            </el-tree>
          </div>
        </cube-scroll>
      </div>
    </div>
    <div class="footer-btn" @click="handleNodeClick" v-show="!noData">
      <button :class="{'btn': confirmBtn}">确定</button>
    </div>
  </div>

</template>

<script type="text/ecmascript">
  import vRow from '../vendor/v-row.vue';
  import vSearch from '../vendor/v-search';
  import { mapGetters, mapMutations } from 'vuex';
  import api from '../utils/api';
  import logger from '../js/utils/logger';
  import BScroll from 'better-scroll';
  import { Scroll } from 'cube-ui';

  export default {
    name: 'search-result',
    components: {vRow, vSearch,cubeScroll: Scroll,},
    data () {
      return {
        hiddenStatus: true, // 隐藏搜索框展开状态(恢复搜索框默认状态)
        pushType: null,
        newArr: [],
        curSelectInfo: [], // 选中节点值
        allSelectArr: [], // 处理全选状态
        showAllSelect: false, // 全选状态
        confirmBtn: false, // 确定按钮
        keywords: '',
        result: [],
        topIcon: true,
        bottomIcon: false,
        data: [],
        filterText: '',
        data2: [],
        defaultProps: {
          children: 'children',
          // label: (data, node) => {
          //   return data && (data.nrUserName || data.label);
          // }
          label: 'nrUserName'
        }
      }
    },
    activated () {
        let pushType = this.$route.query.pushType;
        let isPublish = this.$route.query.isPublish;
        console.log(this.pushType);
        console.log(pushType);
        console.log(isPublish);
        if ((this.pushType !== pushType) || ((this.pushType === pushType) && isPublish)) {
            this.pushType = pushType;
            this.data2 = [];
            this.getRange();
            this.confirmBtn = false;
            this.curSelectInfo = [];
            this.showAllSelect = false;
            this.allSelectArr = [];
            this.filterText = '';
        }
      // 获取当前发布页是从哪个页面过来的
      this.$route.query.origin = this.getOriginType;
      if (this.$route.query.origin === 'listPage') {
        this.data2 = [];
        this.getRange();
        this.confirmBtn = false;
        this.curSelectInfo = [];
        this.showAllSelect = false;
        this.allSelectArr = [];
        this.filterText = '';
        this.keywords = '';
        this.hiddenStatus = true;
      }
    },
    created () {
      this.getRange();
    },
    mounted() {
      this.$nextTick(() => {
        let scroll=this.scroll = new BScroll(this.$refs.wrapper, {})
      })

    },
    computed: {
      ...mapGetters(['getOriginType']),
      noData () {
        if (this.newArr.length) {
          let arr = this.newArr;
          let flag = arr.every((item, index, arr) => {
            return !item;
          })
          return flag;
        } else {
          return false;
        }
      }
    },
    watch: {
      keywords (val) {
        this.newArr = [];
        this.$refs.tree2.filter(val);
      },
      curSelectInfo (val1) {
        if (this.curSelectInfo.length > 0) {
          this.confirmBtn = true;
        } else {
          this.confirmBtn = false;
        }
      }
    },
    methods: {
      ...mapMutations(['SET_RANGE_DATA','SET_SCORE_RANGE_VALUE','SET_NOTICE_RANGE_VALUE','SET_HOME_RANGE_VALUE', 'SET_ORIGIN_TYPE']),
      // 获取发布范围
      getRange () {
        let vm = this, param = {};
        param = {
          noType: this.$route.query.noType
        };
          api.getRange(param).then(function (res) {
            if (res.data.code - 0 === 0){
            vm.data2 = res.data.data;
            logger.log(vm.data2);
            this.$nextTick(() => {
              vm.refreshHeight()
            })
          }
        }).catch(function (err) {
          logger.log(err);
        })
      },
      getKeys (data) {
        // debugger;
        this.newArr = [];
        this.curSelectInfo = this.$refs.tree2.getCheckedNodes();
        const arr = [];
        // 获取当前选中节点id数组
        for (const node of this.curSelectInfo) {
          if (node.id && node.id !== undefined) {
            arr.push(node.id);
          }
        }
        const dataArr = [];
        for (const value of this.data2) {
          if (value && value.id) {
            dataArr.push(value.id);
          }
        }
        // 当前选中节点id 与请求过来的id 对比 如果相等则展示全选按钮
        if (arr.length === dataArr.length) {
          this.showAllSelect = true;
        } else {
          this.showAllSelect = false;
        }
        console.log(this.curSelectInfo);
        // debugger;
      },
      filterNode (value, data) {
        if (!value) return true;
        this.newArr.push(data.nrUserName.indexOf(value) !== -1);
        return data.nrUserName.indexOf(value) !== -1;
      },

      // 确认按钮
      handleNodeClick () {
        let vm = this, param = {}, rangText = '';
        if (!vm.confirmBtn) return;
        // 获取当前选中的节点信息
        // console.log(vm.curSelectInfo, '99999');
        const newArr = [];
        const textArr = [];
        for (const node of vm.curSelectInfo) {
          const [ciId, nrType, nrUserId, nrUserName] = [node.ciId, node.nrType, node.nrUserId, node.nrUserName];
          if (nrType !== undefined && nrUserId  !== undefined && ciId !== undefined) {
            newArr.push({
              ciId,
              nrType,
              nrUserId
            });
            textArr.push(nrUserName);
          }
        }
        rangText = textArr.join('、');
        console.log(newArr);
        console.log(rangText);
        this.SET_RANGE_DATA(newArr);
        let pushType = this.$route.query.pushType;
        if (pushType === 0) {
          // 通知
          this.SET_NOTICE_RANGE_VALUE(rangText);
        } else if (pushType === 1) {
          // 作业
          this.SET_HOME_RANGE_VALUE(rangText);
        } else if (pushType === 2) {
          // 成绩
          this.SET_SCORE_RANGE_VALUE(rangText);
        }
        this.SET_ORIGIN_TYPE('searchPage');
        vm.$router.go(-1);
      },
      /**
       * 全选按钮点击
       */
      allSelectClick () {
        this.allSelectArr = [];
        this.showAllSelect = !this.showAllSelect;
//        this.confirmBtn = !this.confirmBtn;
        for (var i = 0;i < this.data2.length;i++) {
          let id = this.data2[i].id;
          // this.allSelectArr.push(id);
          // this.$set(this.allSelectArr, this.allSelectArr.length, id)
          this.$refs.tree2.setChecked(id, this.showAllSelect, true);
        }
        this.getKeys();
        // console.log(this.allSelectArr);
      },
      refreshHeight(){
        this.scroll.refresh()
      }

    }
  };

</script>

<style rel="stylesheet/scss" lang="scss">
  @import "../scss/variables";
  @import "../scss/mixins";
  .search-result{
    padding-bottom: 54px;
    width: 100%;
    height: 100%;
    /*overflow: scroll;*/
    z-index: 2;
    background: #F2F2F4;
    .scroll-wrap {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 54px;
      overflow: scroll;
    }
    .page-form-search-result {
      padding: 0 ($grid-gutter-width / 2);
      position: relative;
      .no-result{
        position: absolute;
        top:155px;
        left:130px;
        width:120px;
        height:120px;
        background:url('../assets/search-icon.png') no-repeat center;
        background-size:100%;
        .font{
          text-align: center;
          margin-top: 150px;
          font-size: 15px;
          color: #606060;
          line-height: 20px;
        }
      }


    }
    .allSelect {
      padding: 12px 12px 0 12px;
      width: 100%;
      height: 44px;
      border-bottom: 1px solid #e1e1e1;/*no*/
      background-color: #fff;
      .icon-all-select{
        float: left;
        margin-right: 3px;
        width: 22px;
        height: 22px;
        line-height: 30px;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        border: 1px solid #dcdfe6; /*no*/
        vertical-align: middle;
        &.active{
          /*background: url("../assets/Checked@3x.png") no-repeat center;*/
          background: url("../assets/Checked.png") #C3303A no-repeat;
          background-size: cover;
          border-color: rgba(0, 0, 0, 0.1) !important;
        }
      }
      .allText{
        float: left;
        margin-left: 5px;
        font-size: 15px;
        line-height: 23px;
        color: #383838;
      }
    }

    .el-tree-node__expand-icon{
      /*display: none;*/
      position: absolute;
      right: 17px;
      /*background:url('../assets/top-arrow.png') no-repeat center;*/
      /*background-size:100%;*/
    }
    .el-tree-node__label{
      font-size: 15px;
    }

    .el-tree-node__content{
      position: relative;
      margin-left:12px;
      height:44px;
      line-height: 44px;
      border-bottom: 1px solid #e1e1e1;/*no*/
    }
    .el-checkbox__inner{
      width: 22px;
      height: 22px;
      border-radius: 100%;
    }
    .el-checkbox__inner:after{
      background:#C3303A;
    }
    .el-checkbox__input.is-focus .el-checkbox__inner{
      border-color:#dcdfe6;
    }
    .el-checkbox__input.is-checked .el-checkbox__inner{
      background: url("../assets/Checked.png") #C3303A no-repeat;
      background-size: cover;
    }
    /*@media only screen and (-webkit-min-device-pixel-ratio:2){*/
      /*.el-checkbox__input.is-checked .el-checkbox__inner{*/
        /*background: url("../assets/Checked.png") center no-repeat !important;*/
      /*}*/
    /*}*/
    /*@media only screen and (-webkit-min-device-pixel-ratio:3){*/
      /*.el-checkbox__input.is-checked .el-checkbox__inner{*/
        /*background: url("../assets/Checked@3x.png") center no-repeat !important;*/
      /*}*/
    /*}*/
    .el-tree-node__content:hover{
      background-color: #fff;
    }
    .el-checkbox__inner:hover {
      border-color: #dcdfe6;
    }
    .el-checkbox__inner::after{
      width:0;
      height:0;
      border: none !important;
    }
    .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
      border-color:rgba(0,0,0,0.1)!important;
    }
    .el-tree-node__expand-icon{
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
    }
    .el-tree-node__expand-icon.expanded {
      -webkit-transform: rotate(180deg)!important;
      transform: rotate(180deg)!important;
    }
    .el-checkbox__input.is-indeterminate .el-checkbox__inner{
      /*border-color:#C3303A;*/
      /*background: url("../assets/half-checked.png") center  no-repeat !important;*/
      /*background: url("../assets/Half-checked@3x.png") center  no-repeat !important;*/
      /*background: url("../assets/Half-checked@1x.svg") center  no-repeat !important;*/
      background-size: 100%;
    }
    @media only screen and (-webkit-min-device-pixel-ratio:2){
      .el-checkbox__input.is-indeterminate .el-checkbox__inner{
        background: url("../assets/Half-checked.png") center  no-repeat !important;
      }
    }
    @media only screen and (-webkit-min-device-pixel-ratio:3){
      .el-checkbox__input.is-indeterminate .el-checkbox__inner{
        background: url("../assets/Half-checked@3x.png") center  no-repeat !important;
      }
    }
    .el-checkbox__input.is-indeterminate .el-checkbox__inner::before{
      background: none;
    }
    .footer-btn {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: pxTorem(54px);
      padding: 8px 30px;
      box-sizing: border-box;
      background: #FFFFFF;
      overflow: hidden;
      background: rgba(255,255,255,0.90);
      box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
      border-top: 1px solid #DDDEE3; /*no*/
      z-index: 3;
      .btn {
        background: #C3303A;
      }
      button {
        width: 315px;
        height: 38px;
        font-size: pxTorem(17px);
        line-height: pxTorem(38px);
        text-align: center;
        color: #FFFFFF;
        background: #DDDEE3;
        border-radius: pxTorem(4px);
      }

    }
    .el-tree-node__content>.el-checkbox{
      margin-right: 12px;
    }
    .el-tree-node__content{
      padding-left:0!important;
    }
    em{
      display: inline-block;
      width:14px;
      height:14px;
    }
    .el-tree-node__children .el-tree-node .el-tree-node__content{
      margin-left:45px!important;

    }
    /*下拉箭头*/

    .el-tree-node__content .el-tree-node__expand-icon{
      width: 14px!important;
      height:14px!important;
      background:url('../assets/top-arrow.png') no-repeat;
      background-size:100%;

    }
    .el-tree-node__expand-icon{
      font-size: 0!important;
      top: 50%;
      margin-top: -5px;
    }
    .search-result .el-tree-node__content .el-tree-node__expand-icon.expanded{
      -webkit-transform: rotate(45deg)!important;
      transform: rotate(45deg)!important;
    }
    .el-tree-node__expand-icon{
      -webkit-transform: rotate(0deg)!important;
      transform: rotate(0deg)!important;
    }
    .el-tree-node__children .el-tree-node__expand-icon{
      display: none!important;
    }
    .top{
      position: absolute;
      top:60px;
      left:350px;

  }
    .bottom{
      position: absolute;
      top:60px;
      left:350px;
      background:url('../assets/bottom-arrow.png') no-repeat center;
      background-size:100%;
    }

  }
  div[role=treeitem].el-tree-node:nth-last-child(2) {
    .el-tree-node__content {
      border-bottom: none;
    }
  }
</style>





