<template>
  <div class="v-search">
    <div class="v-search__bar">
      <!--<label :for="'vSearch'+'_'+_uid" class="v-search__frm" ref="frm" :autofocus="autofocus" @click.stop="onClick">-->
      <label class="v-search__frm" ref="frm" @click.stop="onClick">
        <input
          :id="'vSearch'+'_'+_uid"
          :autofocus="autofocus"
          v-show="visible"
          ref="input"
          type="search"
          v-model="currentValue"
          :placeholder="!visible? placeholder: ''"
          @focus="onFocus"
          @blur="onBlur"
          @keyup.enter="onEnter"
        >
        <span v-if="!searchShow">搜索</span>
        <i class="icon icon-search" v-if="!searchShow"></i>
        <i class="icon icon-search icon-search11" v-if="searchShow"></i>
        <i class="icon icon-error" v-show="visible" @click="visible = false, currentValue = ''"></i>
      </label>
      <a
        class="v-search__bar_cancel"
        @click="visible = false, searchShow = false, currentValue = '',  goList()"
        v-show="visible"
        v-text="cancelText">
      </a>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import { mapMutations } from 'vuex';
  /**
   *
   *              -- Author by Dio Zhu. on 2017.1.11
   */
  export default {
    name: 'v-search',
    props: {
      id: String,
      value: String,
      autofocus: {
        type: Boolean,
        default: false
      },
      cancelText: {
        default: '取消'
      },
      placeholder: {
        default: '搜索'
      },
      result: Array,
      hiddenStatus: Boolean
    },
    data () {
      return {
        visible: false,
        currentValue: this.value,
        searchShow: false
      };
    },

    watch: {
      value (val) {
        this.currentValue = val;
      },

      currentValue (val) {
        this.$emit('input', val);
      }

//            visible (val) {
//                this.currentValue = '';
//                if (val) this.$refs.input.focus();
//            }
    },

    activated () {
//            this.autofocus && this.$refs.input.focus();
      this.$logger.log('v-search.activated!');
      if (this.autofocus) {
        this.visible = true;
        this.$nextTick(() => {
          this.$refs.input.focus();
        });
      }
      if (this.hiddenStatus) {
        this.visible = false;
        this.searchShow = false;
        this.currentValue = '';
      }
    },

    mounted () {
      this.$logger.log('v-search.mounted!');
//            this.autofocus && this.$refs.input.focus();
      if (this.autofocus) {
        this.visible = true;
        this.$nextTick(() => {
          this.$refs.input.focus();
        });
      }
    },

    methods: {
      ...mapMutations(['SET_ORIGIN_TYPE']),
      // 回发布页
      goList () {
        this.SET_ORIGIN_TYPE('searchPage');
        this.$router.go(-1);
      },
      searchFocus () {
        this.visible = true;
        this.$logger.log('v-search.onclick: ', this.visible, this.placeholder);
      },
      onClick () {
        this.visible = true;
        this.$nextTick(() => {
          this.$refs.input.focus();
        });
      },
      onFocus () {
        this.visible = true;
        this.searchShow = true;
        this.$nextTick(() => {
          this.$refs.input.focus();
        });
      },
      onBlur () {
        this.visible = true;
      },
      onEnter () {
        this.$logger.log('v-search.onEnter: ');
        this.$emit('handle-search', this.currentValue);
      }
    }
  };
</script>
<style rel="stylesheet/scss" lang="scss">
  @import "../scss/variables";
  @import "../scss/mixins";

  .v-search {
    /*position: fixed;*/
    top: 0;
    left: 0;
    width: 100%;
    height: pxTorem(44px);
    padding: 0 pxTorem(15px);
    background: $body-bg;
    /*border-bottom: #DDDEE3 1px solid;*/
    span {
      display: block;
      margin-left: pxTorem(148px);
      font-size: pxTorem(14px);
      line-height: pxTorem(28px);
      color: #8E8E93;
    }
    input {
      padding-right: pxTorem(40px);
      width: 100%;
      height: pxTorem(28px);
      font-size: pxTorem(14px);
      /*background: #FFF;*/
      /*-webkit-border-radius: pxTorem(5px);*/
      /*-moz-border-radius: pxTorem(5px);*/
      /*border-radius: pxTorem(5px);*/
      /*padding-left: pxTorem(28px);*/

      appearance: none;
      border: 0;
      box-sizing: border-box;
      outline: 0;

      &::-webkit-input-placeholder {text-align: center;}
      &:-ms-input-placeholder{text-align: center;}
      /*&:focus ~ span {*/
      /*display: none;*/
      /*}*/
      /*<!--&:focus ~ .icon {-->*/
      /*<!--&.icon-search {-->*/
      /*<!--transform: translateX(pxTorem(-146px));-->*/
      /*<!--transition: transform 0.3s ease-in-out;-->*/
      /*<!--}-->*/
      /*<!--&.icon-error {-->*/
      /*<!--display: block;-->*/
      /*<!-- -webkit-transform:rotate(90deg);-->*/
      /*<!--transition: transform 0.3s ease-in-out;-->*/
      /*<!--}-->*/
      /*<!--}-->*/
    }

    .icon {
      position: absolute;
      font-size: pxTorem(12px);
      line-height: 1;
      top: 50%;
      color: #333;
      margin-top: pxTorem(-6px);

      &.icon-search {
        width: pxTorem(12px);
        height: pxTorem(12px);
        left: 50%;
        margin-left: pxTorem(-16px);
      }
      &.icon-search11 {
        left: pxTorem(28px);
      }
      &.icon-error {
        /*display: none;*/
        width: pxTorem(14px);
        height: pxTorem(14px);
        right: pxTorem(54px);
        text-align: center;
        font-size: pxTorem(12px);
        line-height: pxTorem(14px);
        color: #FFF;
        background: #b2b2b2;
        border-radius: 50%;
      }
    }
  }

  .v-search__bar {
    position: relative;
    width: 100%;
    height: 100%;
    @include box_flex;
    @include align_items(center);
    @include justify-content(center);

    > label {
      @include flex_grow(1);
      @include flex_shrink(1);
      @include flex_basis(auto);
    }

    > a {
      @include flex_grow(0);
      @include flex_shrink(0);
      @include flex_basis(auto);
    }
  }

  .v-search__frm {
    width: 100%;
    height: pxTorem(28px);
    font-size: pxTorem(14px);
    background: #FFF;
    -webkit-border-radius: pxTorem(5px);
    -moz-border-radius: pxTorem(5px);
    border-radius: pxTorem(5px);
    padding-left: pxTorem(28px);
  }

  .v-search__bar_cancel {
    /*width: pxTorem(40px);*/
    /*height: pxTorem(18px);*/
    margin-left: pxTorem(10px);
    font-size: pxTorem(17px);
    line-height: 1;
    color: #383838;
  }

</style>



